// ALL THE GENERAL STYLES WILL BE HERE

// @override SLICK DOTS
.slick-dots {
  li {
    button:before {
      color: $grey;
      font-size: 10px;
    }

    &.slick-active {
      button:before {
        color: $teal;
      }
    }
  }
}

// NO RESULTS
.no-results {
  color: $light_black;
}

span.highlight {
  background-color: lighten(#3399ff, 35%);
  border-radius: 4px;
  padding: 0px 6px;
  font-family: $avertaSemiBold;
  border: 1px solid lighten(#3399ff, 30%);
}

b {
  font-family: $avertaSemiBold;
  color: $black;
}

// TABLE
.table {
  font-family: $avertaLight;
  border-collapse: collapse;
  width: 100%;
  border: 1px solid $defaultborder;

  tr {
    border-bottom: 1px solid $defaultborder;

    %table-padding {
      padding: 15px;
    }

    th {
      text-align: left;
      color: darken($light_black, 20%);
      @extend %table-padding;
    }

    td {
      @extend %table-padding;
    }
  }

  tr:nth-child(even) {
    background: $light_grey;
  }

  thead {
    tr {
      background: darken($light_grey, 2%);
    }
  }
}

.button {
  padding: 10px;
  border: 1px solid #e1e1e1;
  display: inline-block;
  cursor: pointer;
  background: $light_grey;
  border-radius: 4px;
  outline: none;
  min-width: 80px;
  transition: background 0.2s;

  &:hover {
    background: darken($light_grey, 4%);
  }

  &:active {
    background: darken($light_grey, 10%);
  }

  &:focus {
    border: 1px solid $blue;
  }
}

.gap {
  width: 10px;
  display: inline-block;
}

