// TEXT STYLES
.text {
  // ALIGNMENTS
  &-left {
    text-align: left;
  }
  &-right {
    text-align: right;
  }
  &-justify {
    text-align: justify;
  }
  &-center {
    text-align: center;
  }

  // HEADINGS
  &-xlg {
    font-size: $extralarge;
    color: $black;
  }
  &-lg {
    font-size: $large;
    color: $black;
  }
  &-md {
    font-size: $medium;
    color: $black;
  }
  &-sm {
    font-size: $small;
    color: $black;
  }
  &-xsm {
    font-size: $extrasmall;
    color: $black;
  }
  &-thin {
    font-size: $thin;
    color: $black;
  }
  &-para {
    font-size: $thin;
    color: $black;
  }
  &-xthin {
    font-size: $extrathin;
    color: $black;
  }
  &-hairline {
    font-size: $hairline;
    color: $black;
  }

  // COLORS
  &-teal {
    color: $teal;
  }
  &-orange {
    color: $orange;
  }
  &-green {
    color: $green;
  }
  &-purple {
    color: $purple;
  }
  &-light-teal {
    color: $light_teal;
  }
  &-white {
    color: $white;
  }
  &-black {
    color: $black;
  }
  &-red {
    color: $red;
  }
  &-blue {
    color: $blue;
  }
  &-light-black {
    color: $light_black;
  }
  &-grey {
    color: $grey;
  }
  &-light-grey {
    color: $light_grey;
  }

  // TRANSFORMS
  &-upper {
    text-transform: uppercase;
  }
  &-lower {
    text-transform: lowercase;
  }
  &-capitalize {
    text-transform: capitalize;
  }
  &-bold {
    font-family: $avertaBold;
  }
  &-italic {
    font-style: italic;
  }
  &-underline {
    text-decoration: underline;
  }
}

